a {
  color: #fff;
}

a:hover, a:focus {
  text-decoration: none;
}

html {
  background: #292929;
  color: #c3c3c3;
  font-size: 0.625em;
}

body {
  font-size: 3rem;
  text-align: center;
}

@media (max-width: 600px) {
  body {
    font-size: 2rem;
  }
}

.container {
  padding: 50px 30px;
  position: relative;
  z-index: 1;
}

@media (max-width: 600px) {
  .container {
    padding-top: 100px;
  }
}

h1 {
  margin-bottom: 0.1em;
}

input {
  color: #292929;
  background-color: #c3c3c3;
  border: 1px solid #000;
  padding: 10px;
  max-width: 100%;
  text-align: left;
  text-align: start;
}

button {
  color: #fff;
  background: #000;
  padding: 10px 20px;
  border: 2px solid;
  border-radius: 10px;
  transition: 0.3s;
}

button:hover, button:focus {
  background: #fff;
  color: #000;
}

.block {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1.5em;
}

.block label {
  display: block;
  margin-bottom: 0.5em;
}

pre {
  margin: 0.5em 0;
  overflow: visible;
  white-space: pre-wrap;
}

pre code {
  font-size: 0.6em;
  background: #000;
  padding: 0.8em 20px;
  border-radius: 10px;
  box-decoration-break: clone;
  line-height: 2;
}

.credit {
  font-size: 1.2rem;
}

.version {
  font-size: 2rem;
  margin-top: 0;
  font-style: italic;
}

.clock {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  height: 90vmin;
  width: 90vmin;
  opacity: 0.3;
}

.visually-hidden {
  position: absolute;
  height: 0;
  width: 0;
  overflow: hidden;
  opacity: 0;
  pointer-events: none;
}

.downloadMethod {
  text-align: center;
  margin: 2em;
}

.downloadMethod h2 {
  font-size: 1em;
  margin-bottom: 0;
}

.downloadMethod p {
  margin-top: 0;
}

.downloadMethod a {
  font-size: 0.7em;
}

.result {
  display: none;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50px;
  max-width: 47rem;
  padding: 50px;
  margin: auto;
  margin-top: 2em;
  outline: none;
}

.result.-visible {
  display: block;
}

.result__heading {
  margin-top: 0;
  padding-bottom: 1rem;
  border-bottom: 1px solid #fff;
}

.result__list {
  margin-bottom: 0;
}

.result__item {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.result__item + .result__item {
  margin-top: 2rem;
}

.result__label {
  color: #fff;
  text-align: right;
}

.result__value {
  margin-left: 1rem;
  min-width: 7ch;
  flex-shrink: 0;
  line-height: 1;
}

.result__close {
  margin-top: 1.5em;
}
